<template>
  <div>
    <div class="document-list-box">
      <div class="document-list-title-box">
        <a-row>
          <a-col :span="20">
            <a class="document-list-title-item document-list-title-activate">文章</a>
            <a class="document-list-title-item" v-if="false">最近</a>
          </a-col>
          <a-col :span="4">
            <div class="document-list-title-search-box">
              <input/>
              <span><a-icon type="search"/></span>
            </div>
          </a-col>
        </a-row>
      </div>
      <div class="document-list-body-box">
        <div class="document-list-body-item" v-for="(document, index) in documentList" :key="index">
          <div class="document-list-body-item-title">
            <a @click="clickRouterLinkHandle(document.path)">
              {{ document.title }}
            </a>
          </div>
          <div class="document-list-body-item-footer">
            <span>{{ document.tag }}</span>
            <a-divider type="vertical"/>
            发布时间 {{ timestamp(document.datetime) }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import apiRequest from "../../../request/api/ApiRequest";
import common from "../../../util/common";

export default {
  name: "DocumentListBar",
  data () {
    return {
      documentList: []
    }
  },
  mounted() {
    apiRequest.getCatalogueList({page: 1, rows: 10}).then(res => {
      if (res.code === 2000) {
        this.documentList = res.data
      }
    })
  },
  methods: {
    clickRouterLinkHandle (title) {
      this.$router.push('/document/' + window.btoa(unescape(encodeURIComponent(title))))
    },
    timestamp (timestamp) {
      return common.timestamp(timestamp)
    }
  }
}
</script>

<style scoped>
.document-list-box{
  width: 100%;
  height: auto;
  min-height: 500px;
  background: #fff;
  border-radius: 2px;
}
.document-list-box .document-list-title-box{
  width: 100%;
  height: 42px;
  padding: 0 1em;
  border-bottom: 1px #f0f0f2 solid;
}
.document-list-box .document-list-title-box .document-list-title-item{
  display: inline-block;
  color: #677788;
  height: 42px;
  padding: 0 .3em;
  line-height: 42px;
  margin-right: 2em;
  transition: .54s;
}
.document-list-box .document-list-title-box .document-list-title-activate{
  color: #333333;
  border-bottom: #333333 solid 2px;
}
.document-list-box .document-list-title-box .document-list-title-item:hover {
  color: #333333;
}
.document-list-box .document-list-title-box .document-list-title-search-box{
  width: 100%;
  height: 28px;
  margin: 7px 0;
  background: #f0f0f2;
  border-radius: 30px;
}
.document-list-box .document-list-title-box .document-list-title-search-box input{
  width: 88%;
  height: 28px;
  outline: none;
  border: none;
  padding: 0 1em;
  font-size: 14px;
  color: #677788;
  background: #f0f0f2;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
}
.document-list-box .document-list-title-box .document-list-title-search-box span{
  display: inline-block;
  font-size: 14px;
  width: 12%;
  height: 28px;
  color: #677788;
  cursor: pointer;
  line-height: 28px;
}


.document-list-body-box{
  width: 100%;
  height: auto;
  padding: 1em;
  max-height: 100vh;
  overflow-y: scroll;
}
.document-list-body-box::-webkit-scrollbar{
  width: 0;
}

.document-list-body-box .document-list-body-item{
  width: 100%;
  height: auto;
  margin-bottom: 1em;
  border-bottom: 1px #f0f0f2 solid;
}
.document-list-body-box .document-list-body-item .document-list-body-item-title{
  margin-bottom: .4em;
}
.document-list-body-box .document-list-body-item .document-list-body-item-title a{
  color: #333333;
  font-size: 18px;
  font-weight: 700;
  transition: .53s;
}
.document-list-body-box .document-list-body-item .document-list-body-item-title a:hover{
  color: #fc5531;
}
.document-list-body-box .document-list-body-item p{
  font-size: 14px;
  margin: .3em 0;
  color: #677788;
}
.document-list-body-box .document-list-body-item .document-list-body-item-footer{
  width: 100%;
  height: auto;
  font-size: 14px;
  margin-bottom: .8em;
}
.document-list-body-box .document-list-body-item .document-list-body-item-footer span{
  display: inline-block;
  padding: .1em .6em;
  font-size: 12px;
  color: #e33e33;
  text-transform: uppercase;
  background: rgba(227,62,51,.1);
  border-radius: 4px;
}
</style>
