import Vue from 'vue'
import Router from 'vue-router'

import IndexView from "../page/frontend/IndexView";
import IndexMain from "../page/frontend/main/IndexMain";
import DocumentMain from "../page/frontend/document/DocumentMain";

Vue.use(Router)

const router =  new Router({
  routes: [
    {
      path: '/',
      name: 'IndexView',
      component: IndexView,
      meta: {auth: false},
      children: [
        {
          path: '/',
          name: 'IndexMain',
          component: IndexMain,
          meta: {auth: false},
        },
        {
          path: '/document/:id',
          name: 'DocumentMain',
          component: DocumentMain,
          meta: {auth: false},
        }
      ]
    }
  ]
})

export default router
