import Mock from 'mockjs'
import globalData from './data.json'

Mock.mock('/luochen/data', 'get', () => {
  return globalData
})

Mock.mock('/luochen/document/details', 'get', action => {
  let param = JSON.parse(action.body)
  for (const document of globalData.documentList) {
    if (document.id === param.id) {
      return {code: 2000, data: document, message: '获取成功'}
    }
  }
  return {code: 4004, data: {}, message: '查询的key不存在'}
})

Mock.mock('/luochen/document/list', 'get', action => {
  let params = JSON.parse(action.body)
  let page = params.page ? params.page : 1
  let rows = params.rows ? params.rows : 4
  let start = (page - 1) * rows
  let end = start + rows
  let documentList = JSON.parse(JSON.stringify(globalData.documentList)).reverse()
  let result = {
    list: documentList.slice(start, end),
    total:  documentList.length
  }
  return {code: 2000, data: result, message: '获取成功'}
})

Mock.mock('/luochen/document/recom/list', 'get', () => {
  let num = 0
  let documentList = []
  let documents = JSON.parse(JSON.stringify(globalData.documentList)).reverse()
  for (let i = 0; i < documents.length; i++) {
    if (documents[i].label.length > 1) {
      documentList.push(documents[i])
      num++;
    }
    if (num === 4) break
  }
  return {code: 2000, data: documentList, message: '获取成功'}
})

Mock.mock('/luochen/tag/list', 'get', (action) => {
  let params = JSON.parse(action.body)
  let page = params.page ? params.page : 1
  let rows = params.rows ? params.rows : 4
  let start = (page - 1) * rows
  let tagList = []
  let num = 0
  for (let i = start; i < globalData.tagList.length; i++) {
    if (num === rows) break
    tagList.push(globalData.tagList[i])
    num ++
  }
  return {code: 2000, data: tagList, message: '获取成功'}
})


Mock.mock('/luochen/document/new/list', 'get', () => {
  let documentList = JSON.parse(JSON.stringify(globalData.documentList)).reverse()
  return {code: 2000, data: documentList.slice(0, 10), message: '获取成功'}
})


Mock.mock('/luochen/document/statistics', 'get', () => {
  let result = {}
  result.document = globalData.documentList.length
  let recom = 0
  for (const document of globalData.documentList) {
    if (document.label.length > 0) recom += 1
  }
  result.recom = recom
  result.tag = globalData.tagList.length
  if (result.document > 0) {
    result.datetime = globalData.documentList[result.document - 1].datetime
  } else {
    result.datetime = 0
  }
  return {code: 2000, data: result, message: '获取成功'}
})


Mock.mock('/luochen/navigation/list', 'get', () => {

  return {code: 2000, data: globalData.navigationList, message: '获取成功'}
})

Mock.mock('/luochen/catalogue/list', 'get', () => {
  return {code: 2000, data: globalData.catalogueList, message: '获取成功'}
})




