<template>
  <div>

    <div class="document-main-load-box" v-show="loadingStatus">
      <a-spin size="large" tip="玩命加载中..."/>
    </div>

    <div class="document-main-box" v-show="!loadingStatus">

      <a-row :gutter="[16,16]" v-if="false">
        <a-col :span="5">
          <div class="main-card-item-box catalogue">
            <main-card-bar>
              <span slot="title">目录</span>
            </main-card-bar>
          </div>
        </a-col>
        <a-col :span="19">
          <div class="document-main-right-box">
            <div class="document-main-content-box">
            </div>
          </div>
        </a-col>
      </a-row>

      <div class="document-main-content-box">
        <component v-bind:is="document" key="document"></component>
      </div>

    </div>

  </div>
</template>

<script>
import common from "../../../util/common";
import MainCardBar from "../../../components/frontend/main/MainCardBar";

import 'highlight.js/styles/github.css'
import 'github-markdown-css'

export default {
  name: "DocumentMain",
  data () {
    return {
      loadingStatus: false,
      document: undefined
    }
  },
  mounted() {
    this.loadingStatus = true
    let documentId = this.$route.params.id
    if (documentId) {
      let documentPath = decodeURIComponent(escape(window.atob(documentId)))
      this.document = () => import('../../../assets/documents' + documentPath)
    } else {
      this.$router.push('/')
    }
    this.loadingStatus = false
  },
  methods: {
    timestampShow (timestamp) {
      return common.timestampShow(timestamp)
    },
    simpleTimestamp (timestamp) {
      return common.simpleTimestamp(timestamp)
    }
  },
  components: { MainCardBar }
}
</script>
<style scoped>
.document-main-load-box{
  position: fixed;
  width: 100%;
  height: 100vh;
  text-align: center;
  padding-top: 10rem;
}

.document-main-box{
  width: 100%;
  height: auto;
  padding: 2em;
  background: #EBF4FF;
}
.document-main-box .main-card-item-box{
  margin-bottom: 1em;
}
.document-main-box .document-main-content-box{
  width: 100%;
  background: #fff;
  border-radius: 2px;
  padding: 2em;
}

/* 字体大小 */
.document-main-box .document-main-content-box >>> *{
  font-size: 18px;
}
/* 字体大小 */

/* 标题 */
.document-main-box .document-main-content-box >>> h1, .document-main-box .document-main-content-box >>> h2,
.document-main-box .document-main-content-box >>> h3, .document-main-box .document-main-content-box >>> h4,
.document-main-box .document-main-content-box >>> h5, .document-main-box .document-main-content-box >>> h6{
  margin-top: 1em;
  margin-bottom: 0.5em;
  font-weight: 600;
  line-height: 1.2;
  color: #1e2022;
}
.document-main-box .document-main-content-box >>> h1{font-size: calc(1.475rem + 2.7vw);}
.document-main-box .document-main-content-box >>> h2{font-size: calc(1.425rem + 2.1vw);}
.document-main-box .document-main-content-box >>> h3{font-size: calc(1.3375rem + 1.05vw);}
.document-main-box .document-main-content-box >>> h4{font-size: calc(1.2875rem + 0.45vw);}
.document-main-box .document-main-content-box >>> h5{font-size: calc(1.2625rem + 0.15vw);}
.document-main-box .document-main-content-box >>> h6{font-size: 1.125rem;}
/* 标题 */

/* 大标题 */
.document-main-box .document-main-content-box >>> h1:first-child{
  text-align: center;
  margin-top: 0;
  padding-bottom: .3em;
  border-bottom: #f9fafc solid 1px;
}
/* 大标题 */


/* 代码块 */
.document-main-box .document-main-content-box >>> pre{
  position: relative;
  background: #282c34;
  color: #abb2bf;
  padding: 30px 10px 10px 10px;
  font-size: 14px;
  border-radius: 4px;
}
.document-main-box .document-main-content-box >>> pre::before{
  content: '';
  position: absolute;
  top: 9px;
  left: 10px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #fc625d;
  box-shadow: 20px 0 #fdbc40, 40px 0 #35cd4b;
  z-index: 1;
}
/* 代码块 */

/* 图片 */
.document-main-box .document-main-content-box >>> img{
  display: block;
  margin: 0 auto;
}
/* 图片 */

/* 表格 */
.document-main-box .document-main-content-box >>> table {
  border: #e7eaf3 solid 1px;
}
.document-main-box .document-main-content-box >>> table tr th,
.document-main-box .document-main-content-box >>> table tr td{
  padding: .6em 1em;
  border: #e7eaf3 solid 1px;
}
/* 表格 */

</style>
