const common = {

  textShow (text) {
    if (text && text.length > 163) {
      return text.substring(0, 163) + '...'
    }
    return text
  },

  timestamp (timestamp) {
    let datetime = new Date(timestamp)
    return datetime.getFullYear() + '-' + this.addZero((datetime.getMonth() + 1)) + '-' +
        this.addZero(datetime.getDate()) + ' ' + this.addZero(datetime.getHours()) + ':' +
        this.addZero(datetime.getSeconds())
  },

  timestampShow (timestamp) {
    let datetime = new Date(timestamp)
    return datetime.getFullYear() + '-' + this.addZero((datetime.getMonth() + 1)) + '-' + this.addZero(datetime.getDate())
  },

  simpleTimestamp (timestamp) {
    let datetime = new Date(timestamp)
    return this.addZero((datetime.getMonth() + 1)) + '/' + this.addZero(datetime.getDate())
  },

  addZero (num) {
    return num > 9 ? num : '0' + num
  },

  getuuid () {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
      let r = Math.random()*16|0, v = c === 'x' ? r : (r&0x3|0x8);
      return v.toString(16);
    })
  },

  getRandom () {
    return Math.ceil(Math.random() * 10000) + 100;
  },

  saveFile (path, info) {
    // eslint-disable-next-line no-undef
    saveAs(new File([info], path, { type: 'text/plain;charset=utf-8' }))
  },

  documentListColumns () {
    return [
      {title: '文章标题', dataIndex: 'title-info', key: 'title-info', width: 380, scopedSlots: { customRender: 'title-info' }},
      {title: '阅读 | 点赞 | 评论', dataIndex: 'number', key: 'number', scopedSlots: { customRender: 'number' }},
      {title: '文章标签集', dataIndex: 'tagList', key: 'tagList', scopedSlots: { customRender: 'tagList' }},
      {title: '创建时间', dataIndex: 'datetime', key: 'datetime', scopedSlots: { customRender: 'datetime' }},
      {title: '文章状态', dataIndex: 'status', key: 'status', scopedSlots: { customRender: 'status' }},
      {title: 'action', dataIndex: 'action', key: 'action', scopedSlots: { customRender: 'action' }},
    ]
  },

  navListColumns () {
    return [
      {title: '导航分组', dataIndex: 'group', key: 'group'},
      {title: '链接数', dataIndex: 'groupNum', key: 'groupNum', scopedSlots: { customRender: 'groupNum' }}
    ]
  },

  navListSubColumns () {
    return [
      {title: '导航名称', dataIndex: 'name', key: 'name'},
      {title: '导航链接', dataIndex: 'link', key: 'link'},
      {title: '链接图片', dataIndex: 'img', key: 'img', scopedSlots: { customRender: 'img' }}
    ]
  }

}

export default common
